import { jsxs as _jsxs } from "react/jsx-runtime";
import React from 'react';
const Timer = ({ isAnswer }) => {
    const [timer, setTimer] = React.useState(60);
    React.useEffect(() => {
        setInterval(() => {
            setTimer((pre) => pre - 1);
        }, 1000);
    }, []);
    React.useEffect(() => {
        if (timer == 0) {
            isAnswer(false);
        }
    }, [timer]);
    return _jsxs("div", { children: ["\u0412\u0440\u0435\u043C\u044F: ", timer] });
};
export default Timer;
